
.cell__cipherkey{width:100%
}
.cell__cipherkey .hex--hex,.exp--column .exp--item{width:67px
}
.exp--container{box-sizing:border-box;overflow-y:visible;display:flex;flex-flow:row nowrap;height:162px;width:100%;position:relative;overflow-x:scroll
}
.exp--column{display:flex;flex-flow:column nowrap;align-content:flex-start;height:160px;position:relative;box-sizing:border-box;border:1px solid transparent
}
.exp--column:after{top:0;left:1em;position:absolute;width:12em;content:attr(col);color:#ccc;z-index:-1
}
.exp--item{height:40px;line-height:40px;flex:0 0 25%;text-align:center;color:#aaa
}
.column__extra:before{bottom:0;left:1em;position:absolute;width:12em;content:"Round Key " attr(roundkey);font-size:22px;font-weight:900;color:#dfdfdf;z-index:-1
}
.column__chosen{border:1px solid #000
}
.column__sup{border:1px solid #aaa
}
.column__extra{border-left:2px solid #000;position:relative
}
.exp--more{display:inline-flex;align-items:center;justify-content:flex-start;white-space:nowrap
}
.inner--more{overflow-x:scroll
}



