
@media screen and (max-width: 768px){
.btn-group--fixed{position:fixed;top:0;left:-1rem;overflow-x:scroll;width:calc(100% + 1rem);background:#fff;z-index:99;display:flex;flex-flow:row nowrap;box-shadow:0 0 1px 2px #333
}
}
#app{padding:45px 0
}
body{font-size:14px;overflow-x:hidden;overflow-y:scroll;margin:0;padding:0;font-weight:normal;font-style:normal;font-stretch:normal;line-height:1.79;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-webkit-tap-highlight-color:transparent;text-rendering:optimizeLegibility;-webkit-text-size-adjust:100%;text-size-adjust:100%;-webkit-touch-callout:none
}
[data-dpr='2'] body{font-size:28px
}
[data-dpr='3'] body{font-size:42px
}



