
[column="0"]:before{position:absolute;top:50%;left:0;-webkit-transform:translate(-100%, -50%);transform:translate(-100%, -50%);content:attr(row);color:#aaa
}
[row="0"]:after{position:absolute;top:0;left:50%;-webkit-transform:translate(-50%, -100%);transform:translate(-50%, -100%);content:attr(column);color:#aaa
}
.sbox--container{margin-top:2em;display:flex;flex-flow:row wrap;align-content:flex-end;justify-content:flex-end;position:relative
}
.sbox--container .sbox--item{position:relative;text-align:center;height:24px;line-height:24px;flex:0 0 6%
}
.hex--chosen{border:1px solid #333
}



