
.fade-enter-active,.fade-leave-active{transition:all .5s
}
.fade-enter,.fade-leave-active{opacity:0;height:0
}
.card{box-sizing:border-box;margin-bottom:20px
}
.hex--container{display:flex;flex-flow:column wrap;align-content:flex-start;height:160px;position:relative;overflow-x:scroll
}
.hex--container .hex--hex{width:25%;flex:0 0 25%
}
.hex--container .hex--hex p{padding:0;margin:0;color:#aaa
}
.hex--container .form-control{width:50%
}
.hex--hex,.hex--item,.go{display:flex;justify-content:center;align-items:center
}



